import React, {Component} from 'react';
import './App.css';

import Header from './Header.js'
import Home from './Home.js';
import DisplayNode from './DisplayNode.js';
const ROOT_URL = 'https://clincoln8.github.io/test-git-pages/'

class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      subjNodes:[],
      nodeHash: [],
      curDCID:null,
    }
  }
  componentDidMount(){
    const self = this;

    window.addEventListener('hashchange', () => {
      console.log('in listener' + window.location.hash)
      let params = new URLSearchParams(window.location.hash.split("#")[1]);
      let searchDCID = params.get('dcid');
      if (searchDCID) {
        self.setState({curDCID:searchDCID});
      } else{
        self.setState({curDCID:null});
      }

      console.log(self.state.curDCID)
    }, false);
  }

  setAppState = (dcid) => {
    window.location.hash = '#dcid=' + dcid;
  }


  render = () => {

    if (window.location.href === ROOT_URL) return (
      <div>
      <Header setAppState={this.setAppState}/>
      <Home setAppState={this.setAppState} appState={this.state}/>
      </div>
    );
    if (this.state.curDCID){
      console.log('from render: ' + this.state.curDCID)
     return(
      <div>
      <Header setAppState={this.setAppState}/>
      <DisplayNode key={this.state.curDCID} appState={this.state} dcid={this.state.curDCID}/>
      </div>
    );
  }

/*
    const search = (window.location.href).replace(ROOT_URL, '');
    let params = new URLSearchParams(search);
    let searchDCID = params.get('dcid');
    if (searchDCID) return (<DisplayNode appState={this.state} dcid={searchDCID}/>);
*/
    return (

      <h2>Requested url could not be found</h2>
    );
  }
}

export default App;
