import React, {Component} from 'react';

class Header extends Component {
  handleSearch = event =>{
    if (event.keyCode === 13){
      this.props.setAppState(event.target.value)
    }
  }
  render(){
    return(
      <div>
        <input type="search" onKeyUp={this.handleSearch}
         placeholder="Search for a dcid..." />
      </div>
    )
  }
}

export default Header
