
import React, {Component} from 'react';
import Node from './GraphNode.js';
import ParseMCF from './ParseMCF.js'

class Home extends Component{
  constructor(props){
    super(props);
    this.state = {
      localDCIDList: this.props.appState.subjNodes,
      fileList: null,
    }
  }

  onSubmitPress = () => {
    const home = this;

    ParseMCF.readFile(this.state.fileList[0]).then( subjList => {
      home.setState({localDCIDList:Object.keys(subjList)});
      })

  };

  onClearPress = () => {
    const home = this;
    home.setState({localDCIDList:[]});
    localStorage.clear();
  }
  render = () =>{
    return (
      <div>
      <div className="App">
      <header className="App-header">
      <h3>Upload an MCF file to preview it in Data Commons.</h3>
      <div>
      <input id="file-select" onChange={ event => this.state.fileList = event.target.files }
        type="file" name="UploadFile" accept=".mcf" required />
      <br></br>
      <button id='submitFile' onClick={this.onSubmitPress} >Submit</button>
      <br></br>
      <button id='clearFile' onClick={this.onClearPress} >Clear</button>
      </div>
      </header>
      </div>
      <ul>{this.state.localDCIDList.map( dcid =>
        <li key={dcid} onClick={() => this.props.setAppState(dcid)}>{dcid}</li>
        )}</ul>
      </div>
    );
  }
}
export default Home;
