import Node from './GraphNode.js';
import React, {Component} from 'react';
const ROOT_URL = 'http://penguin.linux.test:3000/'

function getValueCell(target){
  if(Node.isNode(target)){
    return (<td onClick={() => (window.location.href = ROOT_URL + '?dcid=' +
      target.dcid)}>{target.dcid}</td>);
  } else{
    return (<td>{target}</td>)
  }
}

class AssertionTable extends Component{
  render = () => {
    return (
      <table>
      <thead><tr>
      <th>property</th>
      <th>target</th>
      <th>provenance</th>
      </tr></thead>
    <tbody>{this.props.asserts.map( (assert,index) => {
        return (
          <tr  key={assert.property +  index}>
          <td>{assert.property}</td>
          {getValueCell(assert.target)}
          <td>{assert.provenance}</td>
          </tr>
        );
      })}</tbody>
      </table>
    )
  }
}

class InvAssertionTable extends Component{
  render = () => {
    return (
      <table>
      <thead><tr>
      <th>source</th>
      <th>property</th>
      <th>provenance</th>
      </tr></thead>
    <tbody>{this.props.invAsserts.map( (assert,index) => {
        return (
          <tr  key={assert.property +  index}>
          {getValueCell(assert.src)}
          <td>{assert.property}</td>
          <td>{assert.provenance}</td>
          </tr>
        );
      })}</tbody>
      </table>
    )
  }
}

class DisplayNode extends Component{
  constructor(props)
  {
    super(props);
    this.state = {
      dcid: this.props.dcid,
      asserts:[],
      invAsserts: []
    }
    console.log('render ' + this.props.dcid)
  }
    componentDidMount(){

      let curNode = Node.getNode(this.state.dcid, true);
      curNode.fetchRemoteData(curNode).then( () => {
        let assert = curNode.assertions;
        const assertList = [];
        const invAssertList = [];

        while(assert){
          assertList.push(assert);
          assert = assert.nextAssertion;
        }
        let invAssert = curNode.invAssertions;
        console.log(curNode);
        while(invAssert){
          invAssertList.push(invAssert);
          invAssert = invAssert.invNextAssertion;
          console.log(invAssertList);
        }
        this.setState({asserts:assertList, invAsserts:invAssertList});
    });
  }

  render = () => {
    return(
      <div>
      <h1>Display node: {this.state.dcid}</h1>
      <h3>Assertions</h3>
      <p><u>source: {this.state.dcid}</u></p>
      <AssertionTable asserts={this.state.asserts}/>
      <br></br>
      <h3>Inverse Assertions</h3>
      <p><u>target: {this.state.dcid}</u></p>
      <InvAssertionTable invAsserts={this.state.invAsserts}/>

      </div>
    );
  }
}
export default DisplayNode;
